export default {
	name: 'home',
	data() {
		return {
			tabList: [],
			tabIndex: 0
		}
	},
	created() {
		this.get_label()
	},
	methods: {
		// 调用云函数的方法
		get_label(){
			this.$api.get_label().then(data => {
				this.tabList = data
			})
			// console.log(this.tabList)
		},
		tab(data) {
			console.log('tab....',data.index)
			this.tabIndex = data.index || 0
		},
		changeTabIndex(index) {
			console.log(index)
			this.tabIndex = index
		}
	}
}
